.content {
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    color: #fff;
}

.imageBox {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    background-image: url('../../assets/loginBackground.jpg');
    background-size: cover;
    background-position: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.imageBox h1 {
    font-size: 4rem;
    word-spacing: .5rem;
}

.imageBox span:first-child {
    position: relative;
    -webkit-animation: showup 2s;
            animation: showup 2s;
}

.imageBox span:nth-child(2) {
    position: relative;
    -webkit-animation: slidein 4s;
            animation: slidein 4s;
}

.formContent {
    min-height: 35rem;
    background-color: #3f3f3f;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.formBox {
    width: 30rem;
}

.formTitle {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-bottom: 30px;
    padding-left: 10px;
}

.formTitle h2 {
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 24px;
    font-weight: 400;
    text-align: right;
    margin-top: 10px;
}

.formTitle span {
    display: block;
    color: #999999;
    font-size: 12px;
    margin-top: 5px;
}

.iconCircle {
    width: 56px;
    height:56px;
    border-radius: 28px;
    border: 3px solid #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 25px;
}

.titleRight {
    padding-right: 15px;
}

.btnBox {
    display: -ms-flexbox;
    display: flex;
    padding: 15px 0;
}

.loginBtn {
    -ms-flex: 1 1;
        flex: 1 1;
    background-color: #2a9699 !important;
    border-color: #2a9699 !important;
    font-size: 1.5rem;
}

.btnBox a {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

@-webkit-keyframes showup {
    0% {
        top: -2000px;
    }
    100% {
        top: 0;
    }
}

@keyframes showup {
    0% {
        top: -2000px;
    }
    100% {
        top: 0;
    }
}

@-webkit-keyframes slidein {
    0% {
        left:-220px;
        opacity: 0;
    }
    50% {
        left:-220px;
        opacity: 0;
    }
    100% { 
        left:0px;
        opacity: 1;
    }
}

@keyframes slidein {
    0% {
        left:-220px;
        opacity: 0;
    }
    50% {
        left:-220px;
        opacity: 0;
    }
    100% { 
        left:0px;
        opacity: 1;
    }
}


